.App {
  
  height: 100vh;
  background: linear-gradient(180deg, rgba(217, 175, 217, 0.42) 0%, rgba(96, 209, 221, 0.534) 100%);
}
Form
{
  background: linear-gradient(134.38deg, rgba(255, 255, 255, 0.5) 5.91%, rgba(255, 255, 255, 0.2) 74.73%);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.253);
  backdrop-filter: blur( 8.5px );
  -webkit-backdrop-filter: blur( 8.5px );
  border-radius: 10px;
}
button{
  background: linear-gradient(91.42deg, rgba(84, 142, 148, 0.88) 44.18%, rgba(84, 142, 148, 0.55) 133.2%);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.32);
  
}
input
{
  background: linear-gradient(91.42deg, rgba(207, 207, 207, 0.45) 44.18%, rgba(204, 204, 204, 0.33) 133.2%);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);

}
a{
  color: #548E94;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
